.container > h1 {
  font-family: "Raleway";
  font-size: 30px;
  font-weight: 600;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: left;
  color: #1d1f22;
}
.container > h2 {
  font-family: "Raleway";
  font-size: 30px;
  font-weight: 400;
  line-height: 27px;
  letter-spacing: 0em;
  text-align: left;
  color: #1d1f22;
}
.smallContainer > h1 {
  font-family: "Raleway";
  font-size: 18px;
  font-weight: 300;
  line-height: 160%;
  letter-spacing: 0em;
  text-align: left;
  color: #1d1f22;
}
.smallContainer > h2 {
  font-family: "Raleway";
  font-size: 18px;
  font-weight: 300;
  line-height: 160%;
  letter-spacing: 0em;
  text-align: left;
  color: #1d1f22;
}
.label {
  font-family: "Roboto" /* "Condensed" */;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  letter-spacing: 0em;
  text-align: left;
  height: 18px;
  color: #1d1f22;
  text-transform: uppercase;
}
.productPrice > .price {
  font-family: "Raleway";
  font-size: 24px;
  font-weight: 700;
  line-height: 18px;
  letter-spacing: 0em;
  text-align: left;
  color: #1d1f22;
  height: 46px;
  margin-bottom: 0;
}
.reverseCont > .productPrice > .price {
  font-size: 24px;
  font-weight: 500;
}
.outOfStock {
  margin-top: 0;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;

  line-height: 160%;
  color: white;
  margin-top: 0.4rem;
  display: inline-block;
  width: 120px;
  text-align: center;
  background-color: #da2c38;
}
.inStock {
  margin-top: 0;
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;

  line-height: 160%;
  color: white;
  margin-top: 0.4rem;
  display: inline-block;
  width: 90px;
  text-align: center;
  background-color: #5ece7b;
}
.reverseCont {
  display: flex;
  flex-direction: column-reverse;
}
.reverseCont > .productPrice {
  color: red;
}
@media only screen and (max-width: 460px) {
  .smallContainer > h2 {
    font-size: 15px;
  }
  .smallContainer > h1 {
    font-size: 15px;
  }
  .reverseCont > .productPrice > .price {
    font-size: 20px;
  }
}
