.layout {
  width: 100%;
}
.overlayLayout {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #39374838 /*  rgba(0, 0, 0, 0.8) */;
  z-index: 2;
}

.container {
  display: flex;
  justify-content: space-between;
  height: auto;

  width: 80vw;
  position: relative;
  left: 10vw;
  top: 100px;
  /*  z-index: -1; */
}
.imagesContainer {
  display: flex;
  /*   width: 50%; */
  object-fit: contain;
}
.info {
  width: 50%;
  display: flex;
  flex-direction: column;
  margin-left: 3rem;
}
.info > p {
  color: red;
  font-family: "Raleway";
  font-weight: 600;
  text-transform: capitalize;
}

.orderBtn {
  height: 43px;
  width: 279px;
  position: relative;
  border-radius: 0px;
  background: #5ece7b;
  color: white;
  text-transform: uppercase;
  font-size: 16px;
  border: #5ece7b;
  font-family: "Raleway";
  font-weight: 600;
  cursor: pointer;
  letter-spacing: 1px;
  margin-top: 2rem;
}
.description {
  font-family: "Roboto";
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  letter-spacing: 0em;
  text-align: left;
}
