.slider {
  position: relative;
  width: 200px;
  height: 288px;
  margin-left: 1rem;
}
.slider > img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}
.smallSlider > img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}
.slider > button {
  height: 24px;
  width: 24px;
  font-size: 20px;
  font-weight: 100;
  border-radius: 0px;
  border: none;
  background-color: #000000ba;
  background: rgba(0, 0, 0, 0.73);
  color: white;
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  transform: rotate(180deg);
  cursor: pointer;
}
.slider > button:nth-of-type(2) {
  margin-right: 2rem;
}

.smallSlider {
  position: relative;
  width: 150px;
  height: 185px;
  margin-left: 1rem;
}
