.smallImages {
  width: 70px;
  height: 70px;
  margin-right: 1rem;
}
.smallImages > img {
  width: 100%;
  height: 100%;
  margin-bottom: 0.5rem;
  cursor: pointer;
  object-fit: contain;
}
.smallImages > img:hover {
  transform: scaleX(1.2);
}
.bannerImage {
  height: 551px;
  object-fit: contain;
  position: relative;
}
.bannerImage > img {
  width: 100%;
  height: 100%;
}
.bannerImage > p {
  width: 100%;
  position: absolute;
  top: 30%;
  left: 20%;
  color: black;
  font-weight: 700;
  font-size: 4rem;
}
